  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 40px;
  padding: 50px;
  background: linear-gradient(135deg, var(--color-primary) 0%, var(--color-primary-dark) 100%);
  border-radius: 1rem;
  color: white;
}
@media (max-width: 768px) {
  .sxa-cta .cta-container {
    flex-direction: column;
    text-align: center;
    padding: 40px 30px;
    gap: 30px;
  }
}
.sxa-cta .cta-content {
  flex-grow: 1;
}
.sxa-cta .cta-content .cta-title {
  margin: 0 0 15px 0;
  font-size: 2rem;
  font-weight: 700;
  color: white;
}
@media (max-width: 768px) {
  .sxa-cta .cta-content .cta-title {
    font-size: 1.75rem;
  }
}
.sxa-cta .cta-content .cta-description {
  margin: 0;
  font-size: 1.125rem;
  opacity: 0.95;
  line-height: 1.8;
}
.sxa-cta .cta-actions {
  display: flex;
  gap: 15px;
  flex-shrink: 0;
}
@media (max-width: 576px) {
  .sxa-cta .cta-actions {
    flex-direction: column;
    width: 100%;
  }
}
.sxa-cta .cta-button {
  display: inline-flex;
  align-items: center;
  gap: 8px;
  padding: 14px 28px;
  font-size: 16px;
  font-weight: 600;
  border-radius: 0.375rem;
  text-decoration: none;
  transition: all 0.3s ease-in-out;
  white-space: nowrap;
}
.sxa-cta .cta-button.cta-primary {
  background: white;
  color: var(--color-primary);
}
.sxa-cta .cta-button.cta-primary:hover {
  transform: translateY(-3px);
  box-shadow: 0 1rem 3rem rgba(0, 0, 0, 0.175);
}
.sxa-cta .cta-button.cta-secondary {
  background: transparent;
  color: white;
  border: 2px solid white;
}
.sxa-cta .cta-button.cta-secondary:hover {
  background: rgba(255, 255, 255, 0.1);
  transform: translateY(-3px);
}
.sxa-cta .cta-button svg {
  transition: all 0.15s ease-in-out;
}
.sxa-cta .cta-button:hover svg {
  transform: translateX(5px);
}
.sxa-cta[data-variant=banner] .cta-container {
  background: var(--bg-surface);
  border: 2px solid var(--border-color);
  color: var(--text-primary);
}
.sxa-cta[data-variant=banner] .cta-container .cta-title {
  color: var(--text-primary);
}
.sxa-cta[data-variant=banner] .cta-container .cta-button.cta-primary {
  background: var(--color-primary);
  color: white;
}
.sxa-cta[data-variant=banner] .cta-container .cta-button.cta-secondary {
  color: var(--text-primary);
  border-color: var(--border-color);
